@mixin alert() {
    width: 80%;
    height: 120px;
    color: red;
    background: #eee;
    border-radius: 10px;
}

@mixin ccc() {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.alertEnd {
    margin: 10px auto;
    @include alert();
    @include ccc();
}