@import './detail.css';

@mixin ccc() {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.my-alert {
    background : #eeeeee;
    padding : 15px;
    border-radius : 5px;
    max-width : 500px;
    width : 100%;
    margin : auto;
}

.my-alert p {
    margin-bottom : 0;
}


